<template>
    <div class="movie-info__wrap">
        <header class="movie-info__header">
            <h5 class="movie-info__header_title">Movie view</h5>
            <b-icon-x class="close-icon" @click="closeModal"/>
        </header>
        <div class="movie-info__content">
            <b-row>
                <b-col sm="4">
                    <div class="movie-poster-wrap ">
                        <div class="movie-poster" :style="posterStyle"></div>
                    </div>
                </b-col>
                <b-col sm="8">
                    <h3 class="movie-title">{{ movie.Title }}</h3>
                    <b-form-rating
                    class="movie-rating" 
                        v-model="movie.imdbRating"
                        readonly
                        show-value
                        precision="1"
                        stars="10"
                        show-value-max
                        no-border
                    />
                    <p class="movie-description">{{ movie.Plot }}</p>
                    <div class="mt-3 mb-4">
                        <b-badge variant="success" class="mr-2">{{movie.Year}}</b-badge>
                        <b-badge variant="success" class="mr-2">{{movie.Runtime}}</b-badge>
                        <b-badge variant="success" class="mr-2">{{movie.Genre}}</b-badge>
                        <b-badge variant="success" >{{movie.Language}}</b-badge>
                    </div>
                    <table class="table small">
                        <tbody>
                            <tr>
                                <th>Production</th>
                                <td>{{movie.Production}}</td>
                            </tr>
                            <tr>
                                <th>Country</th>
                                <td>{{movie.Country}}</td>
                            </tr>
                            <tr>
                                <th>Director</th>
                                <td>{{movie.Director}}</td>
                            </tr>
                            <tr>
                                <th>Writer</th>
                                <td>{{movie.Writer}}</td>
                            </tr>
                            <tr>
                                <th>Actors</th>
                                <td>{{movie.Actors}}</td>
                            </tr>
                            <tr>
                                <th>Awards</th>
                                <td>{{movie.Awards}}</td>
                            </tr>
                        </tbody>
                    </table>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>
export default {
    name: "MovieInfoModalContent",
    props: {
        movie: {
            type: Object,
            required: true
        }
    },
    data: () => ({
         defaultPosterBg: 'linear-gradient(52deg, #2b4c59, #988080)'
    }),
    computed: {
        posterStyle() {
            return {
                'background-image': this.posterBg
            };
        },
        posterBg() {
            return this.movie ? `url(${this.movie.Poster})` : this.defaultPosterBg;
        }
    },
    methods: {
        closeModal(){
            this.$emit('closeModal');
        }
    }
};
</script>

<style lang="scss">
    .movie-info{
        &__header{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 1rem;
            background: linear-gradient(52deg, #2b4c59, #988080);
            color: #ffffff;
            &_title {
                margin-bottom: 0;
                line-height: 1.5;
                font-size: 1.23rem;
            }
        }
        &__content {
            padding: 1rem;
            background-color:#ffffff;
        }
    }
    .movie-poster-wrap{
        position: relative;
        padding-bottom: 150%;
        border-radius: 5px;
        overflow: hidden;
        transition: all 0.2 ease;
    }
    .movie-poster{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;

    }
    .movie-title {
        font-size: 3.5rem;
        font-weight: 300;
        line-height: 1.2;
    }
    .movie-rating >>> .b-rating-star,
    .movie-rating >>> .b-rating-value {
        justify-content: flex-start;
        flex-grow: 0;
        font-size: 1.3rem;
        font-weight: 300;
        padding: 0;
    }
    .movie-description {
        font-size: 1.25rem;
        font-weight: 300;
    }
    .close-icon {
        font-size: 24px;
        cursor: pointer;
    }
</style>